html,
body {
  padding: 0;
  margin: 0;
  font-family: "Rubik", sans-serif;
}

.title-wrapper {
  height: 40vw;
  display: flex;
  background-color: #eddbc7;
  justify-content: space-between;
  align-items: center;
  /* gap: 6vw; */
}

.title {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
  font-size: 10vw;
  font-weight: 300;
  flex-grow: 1;
  color: #765e3f;
  line-height: 8vw;
  height: 100%;
  padding: 0 6vw;
}

.title .sub {
  font-size: 6vw;
}

.title-wrapper .profile-img {
  height: 100%;
  z-index: 10;
  box-shadow: -4px 0 10px rgba(0, 0, 0, 0.454);
}

.tightrope {
  position: absolute;
  width: 80vw;
  bottom: 0;
  left: -40px;
}

.about-me,
.follow-me-wrapper,
.footer-wrapper {
  z-index: 15;
  padding: 32px;
  color: #f3e4da;
  background-color: #a06d5d;
  font-size: 20px;
  text-align: center;
  z-index: 10;
}

.about-me {
  padding: 64px 32px;
}

.footer-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10vw;
}

.follow-me-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.follow-me {
  font-size: 22px;
}

.services {
  background-color: #eddbc7;
  height: 50vh;
  max-height: 70vh;
  color: #765e3f;
  padding: 64px 32px;
  font-size: 22px;
  overflow-y: scroll;
  text-align: center;
}

.service-img {
  width: 100%;
  max-height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.top-buttons {
  width: calc(100% - 8px);
  position: fixed;
  top: 8px;
  display: flex;
  gap: 8px;
  z-index: 999;
  justify-content: flex-end;
}

.top-buttons button,
.top-buttons a {
  background-color: #a06d5d;
  color: #eddbc7;
  border: solid 2px #eddbc7;
  border-radius: 4px;
  text-decoration: none;
  font-size: 14px;
  padding: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.divider {
  background-color: #c7c7c770;
  min-height: 2px;
  width: 100px;
  margin: 8px 0;
}

.expand-about-me {
  text-decoration: underline;
  cursor: pointer;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto auto;
  padding: 8px;
  gap: 16px;
}
